import "./styles/index.scss";

//Работа faq секции

const questions = document.querySelectorAll(".faq-question");

questions.forEach((q) =>
  q
    .querySelector(".faq-question-header")
    .addEventListener("click", function () {
      q.querySelector(".faq-question-info").classList.toggle("active");
      q.querySelector(".question-arrow").classList.toggle("active");
    })
);
